.App {
  background-color: #fcb2c5;
  min-height: 100vh;
  /* display: flex; */
}

header {
  background-color: #5a5a97;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-height: 10vh;
}

h1 {
  color: white;
}

.ChatRoomBody {
  display: flex;
  height: 75vh;
}

.login {
  position: fixed;
  width: 340px;
  height: 280px;
  top: 50%;
  left: 50%;
  margin-top: -140px;
  margin-left: -170px;
}

form {
  margin: 0 auto;
  margin-top: 20px;
  border: 2px solid;
  padding: 10px;
}

label {
  color: #555;
  display: inline-block;
  padding-top: 10px;
  font-size: 20px;
}

button {
  background-color: #5a5a97;
  color: white;
  padding-top: 15px;
  padding: 14px 20px;
  border: none;
  cursor: pointer;
  width: 100%;
}

/* Add a hover effect for buttons */
button:hover {
  opacity: 0.8;
}

input {
    font-family: "Helvetica Neue", Helvetica, sans-serif;
    font-size: 12px;
    outline: none;
}
input[type=text],
input[type=password] {
    color: #777;
    width: 99%;
    height: 30px;
}

input[type=submit] {
  align-content: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.userList {
  /* display: flex; */
  border: 1px solid;
  min-width: 20vw;
}

.messageList {
  width: 80vw;
  border: 1px solid;
  overflow-y: scroll;
}

.sectionTitle {
  /* background-color: #5a5a97; */
  color: #5a5a97;
  padding-left: 10px;
  display: flex;
  justify-content: center;
}

.compose {
  display: flex;
  height: 15vh;
}

#composeInputBox {
  height: 100%;
  font-size: 20px;
}

.usersOnline, .messages {
  padding-left: 10px;
}
